
#footer {
  line-height: 40px;
  height: 40px;
  text-align: center;
  font-size: 14px;
  background: #03a9f4;
  color: #fff;
}
@media only screen and (max-width: 560px) {
#footer {
      font-size: 10px;
}
}
.v-spinner {
  position: fixed;
  left: 50%;
  top: 10%;
  -webkit-transform: translate(-55%, -50%);
          transform: translate(-55%, -50%);
}
.loadText {
  position: fixed;
  left: 50%;
  top: 33%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}



